<template>
  <div class="found">
    <div class="navList">
      <van-tabs v-model="active" :line-width="20" :line-height="3" :swipe-threshold="6" :title-active-color="'#ef5852'" @click="onClick">
        <van-tab v-for="(item,index) in classifyList" :key="index" :title="item.cateName"></van-tab>
      </van-tabs>
      <div class="mid">
        <div class="list mescroll" id="list">
          <div class="list-data" id="listData">
            <div class="item" v-for="(item,index) in list" :key="index">
              <img class="left" @click="toDetail(item.shopCode)" :src="$utils.dealImg(item.shopHeader)" alt />
              <div class="flex-mid">
                <div class="flex">
                  <p class="name">{{item.shopName}}</p>
                  <div class="look" @click="toMerchant(item.shopCode)" v-if="item.shopisWeb == 1">
                    <p>去看看</p>
                    <img class="icon" src="../../assets/img/merchantsIcon/right2.png" alt="">
                  </div>
                </div>
                <div class="flex">
                  <div style="flex:2;">
                    <div class="labels" @click="toDetail(item.shopCode)">
                      <div class="label" v-for="(item1,index1) in item.shopTitles" :key="index1" v-show="item1">{{item1}}</div>
                    </div>
                    <!-- <p class="text" @click="toDetail(item.shopCode)">电话:{{item.shopMobile}}</p> -->
                    <p class="text" @click="toDetail(item.shopCode)">地址:{{item.shopAddr}}</p>
                    <p class="text" @click="toDetail(item.shopCode)">营业时间:{{item.shopYingye}}</p>
                  </div>
                  <div class="right">
                    <div class="btn" @click="contact(item.shopMobile)">
                      <img src="../../assets/img/merchantsIcon/telno1.png" alt="">
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Tab, Tabs, Dialog } from "vant";
import { getFoundCateList, getFoundList } from "@/api";
import MeScroll from "mescroll.js";
export default {
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs
  },
  data() {
    return {
      active: 0,
      activeValue: "",
      classifyList: [],
      mescroll: null,
      list: []
    };
  },
  methods: {
    onClick(name, title) {
      this.activeValue = this.classifyList[name].cateCode;
      this.mescroll.scrollTo(0, 0);
      this.mescroll.resetUpScroll();
    },
    toDetail(id) {
      this.$router.push({
        name: "foundDetail",
        query: { id: id }
      });
    },
    toMerchant(shopCode) {
      this.$router.push({
        name: "merchantsIndex",
        query: {
          shopCode: shopCode
        }
      });
    },
    contact(telno) {
      window.location.href = `tel://${telno}`;
    },
    loadCateList() {
      getFoundCateList({
        cateCode: this.$route.query.cateCode
      }).then(res => {
        if (res.code == 0) {
          if (!res.data.length) {
            this.$toast("暂无数据");
            setTimeout(() => {
              this.$router.back();
            });
            return;
          }
          this.classifyList = res.data;
          this.activeValue = this.classifyList[0].cateCode;
          setTimeout(() => {
            this.startMescroll();
          },500);
        } else {
          this.$toast.fail(res.msg);
        }
      });
    },
    loadData(pageNum, pageSize, successCallback, errorCallback) {
      let $this = this;
      let dataObj = {
        pageNum: pageNum,
        pageSize: pageSize,
        cateCode: this.activeValue
      };
      getFoundList(dataObj)
        .then(res => {
          if (res.code == 0) {
            successCallback && successCallback(res.data);
          } else {
            $this.$toast.fail(res.errorMsg);
          }
        })
        .catch(res => {
          errorCallback && errorCallback();
          console.log(res);
        });
    },
    startMescroll() {
      this.mescroll = new MeScroll("list", {
        //在vue的mounted生命周期初始化mescroll,确保此处配置的id能够被找到
        up: {
          callback: this.upCallback, //上拉回调
          //以下参数可删除,不配置
          isBounce: false, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //可配置每页8条数据,默认10
          empty: {
            //配置列表无任何数据的提示
            warpId: "listData",
            icon: require("@/assets/img/nodata.png"),
            tip: "暂无数据"
            //						  	btntext : "去逛逛 >" ,
            //						  	btnClick : function() {
            //						  		alert("点击了去逛逛按钮");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">-- 无更多数据 --</p>',
          lazyLoad: {
            use: true // 是否开启懒加载,默认false
          }
        }
      });
    },
    //上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback: function(page) {
      //联网加载数据
      let $this = this;
      $this.loadData(
        page.num,
        page.size,
        function(curPageData) {
          //curPageData=[]; //打开本行注释,可演示列表无任何数据empty的配置

          //如果是第一页需手动制空列表 (代替clearId和clearEmptyId的配置)
          console.log(curPageData);
          if (page.num == 1) $this.list = [];
          if (curPageData.list) {
            curPageData.list.map(item => {
              item.shopTitles = item.shopTitle.split(",");
              $this.list.push(item);
            });
          }
          //联网成功的回调,隐藏下拉刷新和上拉加载的状态;
          //mescroll会根据传的参数,自动判断列表如果无任何数据,则提示空;列表无下一页数据,则提示无更多数据;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);
          console.log(page);

          //方法二(推荐): 后台接口有返回列表的总数据量 totalSize
          $this.mescroll.endBySize(
            curPageData.list ? curPageData.list.length : 0,
            curPageData.count
          ); //必传参数(当前页的数据个数, 总数据量)
        },
        function() {
          //联网失败的回调,隐藏下拉刷新和上拉加载的状态;
          $this.mescroll.endErr();
        }
      );
    }
  },
  mounted() {
    document.title = "商户列表";
    this.loadCateList();
  }
};
</script>
<style lang="scss" scoped>
.found {
  .navList {
    // margin-bottom: 10px;
  }
  .mid {
    height: calc(100vh - 44px);
  }
  .list {
    border-top: 1pt solid #efefef;
    .item {
      padding: 15px 10px;
      border-bottom: 1pt solid #efefef;
      background: #fff;
      display: flex;
      align-items: center;
      .left {
        width: 100px;
        // height: calc(100px / 1.316);
        margin-right: 10px;
        border-radius: 5px;
      }
      .flex-mid {
        margin: 0 10px 0 0;
        flex: 2;
        position: relative;
        .flex {
          display: flex;
          align-items: center;
          .name {
            font-size: 15px;
            color: #333;
            line-height: 20px;
            flex: 2;
          }
          .look {
            display: flex;
            align-items: center;
            p {
              color: #ea5050;
              font-size: 13px;
              margin-right: 3px;
              line-height: 15px;
            }
            img {
              width: 5px;
            }
          }
        }
        .labels {
          margin: 10px 0 0;
          .label {
            display: inline-block;
            font-size: 13px;
            margin: 0 10px 5px 0;
            background: #ffba60;
            padding: 3px 8px;
            border-radius: 10px;
            color: #fff;
          }
        }
        .text {
          font-size: 13px;
          color: #666;
          line-height: 20px;
        }
      }
      .right {
        text-align: right;
        margin: 5px 0 0;
        .btn {
          display: inline-block;
          img {
            width: 30px;
            height: 30px;
          }
        }
      }
    }
  }
}
</style>
